.comment-thread {
    outline: 3px solid slategray;
}


.comment {
    display: flex;
    flex-direction: column;
}
.comment, .comment * { 
    /* outline: 1px solid red; */
} 

.comment-data {
    display: flex;
    justify-content: space-between;


}

.comment-body {
    flex-basis: 100%;
    margin: 0;
    padding-left: 1em;
    padding-right: 1em;
}

.comment-author {
    margin: 0;
    margin-top: 0.5em;

}
.comment-data-score, .comment-data-created {
    margin: 0;
    margin-bottom: 0.5em;

}

.comment-data p, .comment-author {
    color: #f0c36c; 
}

.reply {
    margin-left: 2em;
    border-left: 1px solid slategray;
}

.comment-author-op {
    color: rgb(255, 187, 0);
    text-decoration:underline;
}

