.post-container {

    display: flex;
    margin-top: 2.5em;
    margin-bottom: 2.5em;
    border-radius: 1em;
    min-width: 40em;
    transition: height 1s ease;
    min-height: 25em;
}

.post-header, .post-header * {
    background-color: rgb(65, 90, 116);
}
.post-header h2, .post-header p {
    margin-top: 0;
    margin-bottom: 0;
}
.post-header h2 {
    padding-top: 0.5em;
}
.post-header p {
    padding-bottom: 0.5em;
}

.post-data, .post-data * {
    background-color: rgb(65, 90, 116);
}

.post-data {
    display: flex;
    justify-content: space-around;
    border-bottom-left-radius: 1em;
    height: 3.5em;
}

.post {
    border-radius: 1em;
    width: 30vw;
    min-width:40em;

}

.post-body img, .post-body video {
    width: 100%;
}




.post-data-num-comments {
    cursor:pointer;
    text-decoration: underline;
    text-decoration-color: slategray;
    transition: text-decoration-color 0.2s ease, color 0.2s ease, transform 0.2s ease;;
}

.post-data-num-comments:hover {
  text-decoration-color: whitesmoke;
    color: whitesmoke;
  }
  
  .post-body, .post-body * {
    background-color: rgb(36,52,71);
  }

  .post-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left:1em;
    padding-right: 1em;
    border-top-left-radius: 1em;
  }

  .post-body {
display: flex;
min-height: 25em;
  }

  .post-body-self-text {
    padding-left: 0.5em;
    padding-right: 0.5em;

  }

  .post-comments {
    overflow: scroll;
    z-index: 1;
    border-top-right-radius: 1em;
    border-bottom-right-radius: 1em;

}
.post-comments, .post-comments * {
    background-color:rgb(65, 90, 116)
}

.post-body p {
  margin-left: auto;
  margin-right: 4em;
}

.subreddit-link {
  display: inline;
  cursor:pointer;
  text-decoration: underline;
  text-decoration-color: slategray;
  transition: text-decoration-color 0.2s ease, color 0.2s ease;
}

.subreddit-link:hover {
  text-decoration-color: whitesmoke;
  color: whitesmoke;
}

.active{
  color: whitesmoke;
  text-decoration-color: whitesmoke;
  position: relative;
  transform:translateY(-0.7em);

}

.post-data select {
  border: none;
  text-align: right;
  position: absolute;
  transform:translateX(290%) translateY(2.2em);
}